<template>
  <div>
    <v-breadcrumbs
      class="breadcrumbsmain"
      :items="itemsbr"
      divider=">"
      large
    ></v-breadcrumbs>

    <v-data-table
      :headers="headers"
      :items="dataobject"
      :search="search"
      class="rounded elevation-6 mx-3 pa-1"
    >

    <template v-slot:item.tanggal="{ item }">
          {{ gettanggal(item.tanggal) }}
      </template>

      <template v-slot:top>
        <v-toolbar flat>          
          
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          
          
          
        </v-toolbar>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "DaftarKas",
  data: () => ({
    itemsbr: [
      {
        text: "KasBank",
        disabled: true,
        href: "breadcrumbs_dashboard",
      },
      {
        text: "Daftar Kas",
        disabled: true,
        href: "breadcrumbs_link_1",
      },
    ],
    
    search: "",
    authtoken: "",
    BaseUrlGet: "",
    headers: [
      {
        text: "Tanggal",
        value: "tanggal",
      },
      { text: "Kode Akun", value: "kodeAkun" },
      { text: "Nama Akun", value: "namaAkun" },
      { text: "Keterangan", value: "keterangan" },
      { text: "Debit", value: "debit" },
      { text: "Kredit", value: "kredit" },
    //   { text: "Actions", value: "actions", sortable: false },
    ],
    dataobject: [],
  }),

  created() {
    this.authtoken = localStorage.getItem("token");
    this.BaseUrlGet = localStorage.getItem("BaseUrlGet");
    this.initialize();
  },

  methods: {
    async initialize() {
      try {
        const response = await axios.get(
          this.BaseUrlGet + "GetKas?limit=all",
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        // console.log(response.data.data.result.data);
        if (response.data.length != 0) {
          this.dataobject = response.data.data.result.data;
        } else {
          console.log("Kosong");
          this.dataobject = [];
        }
      } catch (error) {
        console.error(error);
        if (error.response.status == 401) {
          localStorage.removeItem("token");
          this.$router.push("/");
        } else {
          this.dataobject = [];
        }
      }
    },

    gettanggal(val) {
        var bulanIndo = ['', 'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September' , 'Oktober', 'November', 'Desember'];
 
        var date = val.split(" ")[0];
        var time = val.split(" ")[1];
      
        var  tanggal = date.split("-")[2];
        var  bulan = date.split("-")[1];
        var  tahun = date.split("-")[0];
      
          return tanggal + " " + bulanIndo[Math.abs(bulan)] + " " + tahun;
    },

    // selectedGudang(val){
    //     console.log(val);
    //     if(val != null){
    //       this.gudang = val;
    //     }else{
    //         this.gudang = 'Gd_001';
    //     }
    //     this.initialize();
    // }

  },
};
</script>