<template>
  <div>
    <v-breadcrumbs
      class="breadcrumbsmain"
      :items="itemsbr"
      divider=">"
      large
    ></v-breadcrumbs>

    <v-data-table
      :headers="headers"
      :items="dataobject"
      :search="search"
      class="rounded elevation-6 mx-3 pa-1"
    >

      <template v-slot:item.tanggal="{ item }">
          {{ gettanggal(item.tanggal) }}
      </template>

      <template v-slot:item.totalHutang="{ item }">
          <v-chip color="red" dark> {{ getRupiah(item.totalHutang) }} </v-chip>
          
      </template>

      <template v-slot:item.totalPelunasan="{ item }">
          <v-chip color="green" dark> {{ getRupiah(item.totalPelunasan) }} </v-chip>
          
      </template>

      <template v-slot:item.sisa="{ item }">
          <v-chip color="orange" dark>{{ getRupiah(item.sisa) }} </v-chip>          
      </template>

      <template v-slot:top>
        <v-toolbar flat>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

        </v-toolbar>
      </template>
      
    </v-data-table>

    <v-snackbar
      v-model="snackbar"
      :color="colorsnackbar"
      :timeout="timeoutsnackbar"
    >
      {{ textsnackbar }}
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "DaftarHutang",
  data: () => ({
    itemsbr: [
      {
        text: "Pembelian",
        disabled: true,
        href: "breadcrumbs_dashboard",
      },
      {
        text: "Daftar Hutang",
        disabled: true,
        href: "breadcrumbs_link_1",
      },
    ],

    date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    menu2: false,

    formTitle: "Add Item",
    value: "add",
    dialog: false,
    dialogDelete: false,
    search: "",
    authtoken: "",
    BaseUrlGet: "",
    headers: [
      {
        text: "Kode Cust",
        value: "codeCustomer",
      },
      { text: "nama Cust", value: "namaCustomer" },
      { text: "Total Hutang", value: "totalHutang" },
      { text: "Total Pelunasan", value: "totalPelunasan" },
      { text: "Sisa", value: "sisa" },
      
    //   { text: "Actions", value: "actions", sortable: false },
    ],
    dataobject: [],

    defaultItem: { 
        id: "",
        codeCustomer: "", 
        namaCustomer: "",
        hutang: "", 
        adminMarketing: "",
        keterangan: "",
    },
    
    username:"",

    itemsAkunCust :[],

    snackbar: false,
    textsnackbar: "Test",
    timeoutsnackbar: 2000,
    colorsnackbar: null,
  }),

  created() {
    this.authtoken = localStorage.getItem("token");
    this.BaseUrlGet = localStorage.getItem("BaseUrlGet");
    this.User = JSON.parse(localStorage.getItem("User"));
    if (this.User) {
      this.username = this.User.username;
    }
    // this.getAkunCustomer();
    this.initialize();
  },

  methods: {
    async initialize() {
      try {
        const response = await axios.get(
          this.BaseUrlGet + "GetPiutang?limit=all",
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        // console.log(response.data.data.result.data);
        if (response.data.length != 0) {
          this.dataobject = response.data.data.result.data;
        } else {
          console.log("Kosong");
          this.dataobject = [];
        }
      } catch (error) {
        console.error(error);
        if (error.response.status == 401) {
          localStorage.removeItem("token");
          this.$router.push("/");
        } else {
          this.dataobject = [];
        }
      }
    },     
    

    gettanggal(val) {
        var bulanIndo = ['', 'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September' , 'Oktober', 'November', 'Desember'];
 
        var date = val.split(" ")[0];
        var time = val.split(" ")[1];
      
        var  tanggal = date.split("-")[2];
        var  bulan = date.split("-")[1];
        var  tahun = date.split("-")[0];
      
          return tanggal + " " + bulanIndo[Math.abs(bulan)] + " " + tahun;
    },

    getRupiah(val) {
        var bilangan = val;
	
        var	number_string = bilangan.toString(),
            sisa 	= number_string.length % 3,
            rupiah 	= number_string.substr(0, sisa),
            ribuan 	= number_string.substr(sisa).match(/\d{3}/g);
                
        if (ribuan) {
        var  separator = sisa ? '.' : '';
            rupiah += separator + ribuan.join('.');
        }
        
        return 'Rp. '+rupiah;
    },
  },
};
</script>