<template>
  <div>
    <v-breadcrumbs
      class="breadcrumbsmain"
      :items="itemsbr"
      divider=">"
      large
    ></v-breadcrumbs>

    <v-data-table
      :headers="headers"
      :items="dataobject"
      :search="search"
      item-key="codeOrder"
      class="rounded elevation-6 mx-3 pa-1"
    >

    <template v-slot:item.tanggal="{ item }">
          {{ gettanggal(item.tanggal) }}
      </template>
  

      <template v-slot:item.totalTrx="{ item }">
          {{ getTotalTrx(item.totalHarga, item.totalDiskon, item.diskonKodeUnik, item.biayaExpedisi) }}
      </template>   

      <template v-slot:top>
        <v-toolbar flat>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            class="d-none d-md-block">
          ></v-text-field>
          <v-divider class="mx-4 d-none d-md-block" inset vertical></v-divider>
          <v-spacer class="d-none d-md-block">></v-spacer>
          <!-- <v-btn dark class="mb-2 mr-1" @click="konfirmasiTempo()" color="green">
            <v-icon small>mdi-checkbox-multiple-marked-outline</v-icon> Verifikasi
          </v-btn> -->
          
        </v-toolbar>
      </template>
    </v-data-table>

    <v-snackbar
      v-model="snackbar"
      :color="colorsnackbar"
      :timeout="timeoutsnackbar"
    >
      {{ textsnackbar }}
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "HistoryTrxKonsi",
  data: () => ({
    itemsbr: [
      {
        text: "Penjualan",
        disabled: true,
        href: "breadcrumbs_dashboard",
      },
      {
        text: "HistoryTrxKonsi",
        disabled: true,
        href: "breadcrumbs_link_1",
      },
    ],
    singleSelect: false,
    selected: [],

    formTitle: "Add Item",
    value: "add",
    dialog: false,
    dialogVerifikasi: false,
    search: "",
    authtoken: "",
    BaseUrlGet: "",
    headers: [
      {
        text: "Code Order",
        value: "codeOrder",
      },
      { text: "nama Customer", value: "nama" },
      { text: "Total Barang", value: "totalBarang" },
      { text: "Total Transaksi", value: "totalTrx" },
      { text: "Tanggal", value: "tanggal" },
      
    //   { text: "Actions", value: "actions", sortable: false },
    ],
    dataobject: [],
    listCodeSelected:[],
    codeGudang:'1',


    snackbar: false,
    textsnackbar: "Test",
    timeoutsnackbar: 2000,
    colorsnackbar: null,

    // itemsCustomer: [],
  }),

  created() {
    this.authtoken = localStorage.getItem("token");
    this.BaseUrlGet = localStorage.getItem("BaseUrlGet");
    this.User = JSON.parse(localStorage.getItem("User"));
    if (this.User) {
      this.username = this.User.username;
       this.codeGudang = this.User.location;
    }
    // this.getCustomer();
    this.initialize();
  },

  methods: {
    async initialize() {
      try {
        const response = await axios.get(
          this.BaseUrlGet + "GetTransaksiKonsiVerified?limit=all&codeLocation="+this.codeGudang,
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        console.log(response.data.data.result.data);
        if (response.data.length != 0) {
          this.dataobject = response.data.data.result.data;
        } else {
          console.log("Kosong");
          this.dataobject = [];
        }
      } catch (error) {
        console.error(error);
        if (error.response.status == 401) {
          localStorage.removeItem("token");
          this.$router.push("/");

        }else {
          this.dataobject = [];
        }
      }
    },

    gettanggal(val) {
        var bulanIndo = ['', 'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September' , 'Oktober', 'November', 'Desember'];
 
        var date = val.split(" ")[0];
        var time = val.split(" ")[1];
      
        var  tanggal = date.split("-")[2];
        var  bulan = date.split("-")[1];
        var  tahun = date.split("-")[0];
      
          return tanggal + " " + bulanIndo[Math.abs(bulan)] + " " + tahun;
    },

    getTypeCust(val){
        var typeCust = '-'
        var str = val;
        var substr = str.substring(0, 2);        
        if(substr.toUpperCase()=='GD'){
            typeCust = 'Return Gudang'
        }else{
            typeCust = 'Return Customer'
        }
        return typeCust;
    },

    getTotalTrx(val1, val2, val3, val4){
        // item.totalHarga, item.totalDiskon, item.diskonKodeUnik, item.biayaExpedisi
        var jumlah = parseInt(val1) - parseInt(val2) -parseInt(val3)  + parseInt(val4)
        var bilangan = jumlah;
	
        var	number_string = bilangan.toString(),
            sisa 	= number_string.length % 3,
            rupiah 	= number_string.substr(0, sisa),
            ribuan 	= number_string.substr(sisa).match(/\d{3}/g);
                
        if (ribuan) {
        var  separator = sisa ? '.' : '';
            rupiah += separator + ribuan.join('.');
        }
        
        return 'Rp. '+rupiah;
    },

    getRupiah(val) {
        var bilangan = val;
	
        var	number_string = bilangan.toString(),
            sisa 	= number_string.length % 3,
            rupiah 	= number_string.substr(0, sisa),
            ribuan 	= number_string.substr(sisa).match(/\d{3}/g);
                
        if (ribuan) {
        var  separator = sisa ? '.' : '';
            rupiah += separator + ribuan.join('.');
        }
        
        return 'Rp. '+rupiah;
    },

  },
};
</script>