<template>
  <div>
    <v-breadcrumbs
      class="breadcrumbsmain"
      :items="itemsbr"
      divider=">"
      large
    ></v-breadcrumbs>

    <v-data-table
      :headers="headers"
      :items="dataobject"
      :search="search"
      class="rounded elevation-6 mx-3 pa-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-btn dark class="mb-2 mr-2 d-none d-md-block" @click="showAddModal()" color="green">
            <v-icon small>mdi-plus</v-icon> Tambahkan Baru
          </v-btn>
          <v-btn dark class="mb-2 d-none d-md-block" @click="reset()" color="green">
            <v-icon small>mdi-plus</v-icon> Reset
          </v-btn>

          <!-- Modal Detail -->
          <v-dialog v-model="dialogDetail" max-width="800px">
            <v-card>
              <v-card-title>
                <span class="headline">Detail Royalti</span>
              </v-card-title>
              <v-divider></v-divider>
              <v-card-text>
                <v-container>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <div>
                            <h5>Nama Buku</h5>
                            <h3 class="ml-2">
                              {{ detailItem.namaBuku }}
                            </h3>
                          </div>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="12" md="3">
                          <div>
                            <h5>Jumlah Buku</h5>
                            <h3 class="ml-2">
                              {{ detailItem.totalBukuSemua }}
                            </h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="3">
                          <div>
                            <h5>Jumlah Uang</h5>
                            <h3 class="ml-2">{{ getRupiah(detailItem.totalUangSemua) }}</h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="3">
                          <div>
                            <h5>Royalti %</h5>
                            <h3 class="ml-2">{{ detailItem.royalti }}</h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="3">
                          <div>
                            <h5>Jumlah Royalti</h5>
                            <h3 class="ml-2">{{ getRupiah(detailItem.royaltiUang) }}</h3>
                          </div>
                        </v-col>
                      </v-row>

                      <v-divider class="my-5"></v-divider>
                      <v-row>
                        <v-col cols="12" sm="12" md="6">
                          <div>
                            <h5>Perusahaan</h5>
                            <h3 class="ml-2">
                              {{ detailItem.namaPTSolo }}
                            </h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="3">
                          <div>
                            <h5>Total Buku</h5>
                            <h3 class="ml-2">
                              {{ detailItem.totalBukuSolo }}
                            </h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="3">
                          <div>
                            <h5>Total Uang</h5>
                            <h3 class="ml-2">
                              {{ getRupiah(detailItem.totalUangSolo) }}
                            </h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="12">
                          <div>
                            <v-data-table
                                :headers="headerslistItem"
                                :items="detailItem.listItemSolo"
                                class="rounded elevation-2 mx-3 pa-1"
                                >
                                <template v-slot:item.hargaSatuan="{ item }">
                                    {{ getRupiah(item.hargaSatuan) }}
                                </template>
                                <template v-slot:item.totalDiskon="{ item }">
                                    {{ getRupiah(item.totalDiskon) }}
                                </template>
                                <template v-slot:item.totalUang="{ item }">
                                    {{ getRupiah(item.totalUang) }}
                                </template>
                            </v-data-table>
                          </div>
                        </v-col>
                      </v-row>

                      <v-divider class="my-5"></v-divider>
                      <v-row>
                        <v-col cols="12" sm="12" md="6">
                          <div>
                            <h5>Perusahaan</h5>
                            <h3 class="ml-2">
                              {{ detailItem.namaPTJakarta }}
                            </h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="3">
                          <div>
                            <h5>Total Buku</h5>
                            <h3 class="ml-2">
                              {{ detailItem.totalBukuJakarta }}
                            </h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="3">
                          <div>
                            <h5>Total Uang</h5>
                            <h3 class="ml-2">
                              {{ getRupiah(detailItem.totalUangJakarta) }}
                            </h3>
                          </div>
                        </v-col>
                        <v-col cols="12" sm="12" md="12">
                          <div>
                            <v-data-table
                                :headers="headerslistItem"
                                :items="detailItem.listItemJakarta"
                                class="rounded elevation-2 mx-3 pa-1"
                                >
                                <template v-slot:item.hargaSatuan="{ item }">
                                    {{ getRupiah(item.hargaSatuan) }}
                                </template>
                                <template v-slot:item.totalDiskon="{ item }">
                                    {{ getRupiah(item.totalDiskon) }}
                                </template>
                                <template v-slot:item.totalUang="{ item }">
                                    {{ getRupiah(item.totalUang) }}
                                </template>
                            </v-data-table>
                          </div>
                        </v-col>
                      </v-row> 

                      <v-divider class="my-5"></v-divider>  
                </v-container>
              </v-card-text>
              
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline">Pilih Semester dan Tahun</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="6">
                      <v-select
                        v-model="idSemester"
                        :items="itemsSemester"
                        item-value="value"
                        item-text="text"
                        label="Pilih Semester"
                        clearable
                        :rules="[(v) => !!v || 'Field is required']"
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                      <v-select
                        v-model="idTahun"
                        :items="itemsTahun"
                        item-value="value"
                        item-text="text"
                        label="Pilih Tahun"
                        clearable
                        :rules="[(v) => !!v || 'Field is required']"
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="12" md="12">
                      <v-text-field
                        v-model="royalti"
                        label="Masukkan Default Royalti"
                        :rules="[(v) => !!v || 'Field is required']"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogEdit" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline">Edit Royalti Buku</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                    <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <div>
                            <h5>Nama Buku</h5>
                            <h3 class="ml-2">
                              {{ namaBukuEdit }}
                            </h3>
                          </div>
                        </v-col>
                      </v-row>
                  <v-row>                    
                    <v-col cols="12" sm="12" md="12">
                      <v-text-field
                        v-model="royaltiEdit"
                        label="Masukkan Royalti"
                        :rules="[(v) => !!v || 'Field is required']"
                        type = 'number'
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="saveEdit"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="headline"
                >Are you sure you want to reset this item?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="resetItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon class="mr-2" @click="getDetail(item)" color="blue">
          mdi-eye
        </v-icon>
        <v-icon @click="editItem(item)" color="warning"> mdi-pencil </v-icon>
      </template>
    </v-data-table>

    <v-snackbar
      v-model="snackbar"
      :color="colorsnackbar"
      :timeout="timeoutsnackbar"
    >
      {{ textsnackbar }}
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Royalti",
  data: () => ({
    itemsbr: [
      {
        text: "Penjualan",
        disabled: true,
        href: "breadcrumbs_dashboard",
      },
      {
        text: "Royalti",
        disabled: true,
        href: "breadcrumbs_link_1",
      },
    ],

    formTitle: "Add Item",
    value: "add",
    dialog: false,
    dialogDelete: false,
    dialogDetail: false,
    dialogEdit : false,
    search: "",
    authtoken: "",
    BaseUrlGet: "",
    headers: [
      {
        text: "judul Buku",
        value: "judulBuku",
      },
      { text: "royalti", value: "royalti" },
      { text: "tahun", value: "tahun" },
      { text: "semester", value: "semester" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    dataobject: [],

    itemsSemester:[
        { text: "Semester 1", value: "1" },
        { text: "Semester 2", value: "2" }
    ],
    itemsTahun:[
        { text: "2019", value: "2019" },
        { text: "2020", value: "2020" },
        { text: "2021", value: "2021" },
        { text: "2022", value: "2022" },
        { text: "2023", value: "2023" },
        { text: "2024", value: "2024" },
        { text: "2025", value: "2025" },
        { text: "2026", value: "2026" },
        { text: "2027", value: "2027" },
        { text: "2028", value: "2028" },
        { text: "2029", value: "2029" },
        { text: "2030", value: "2030" },
        { text: "2031", value: "2031" },
        { text: "2032", value: "2032" },
        { text: "2033", value: "2033" },
        { text: "2034", value: "2034" },
        { text: "2035", value: "2035" },
    ],

    detailItem: { 
        namaBuku: "",
        royalti: "", 
        royaltiUang: "",
        totalBukuSemua: "", 
        totalUangSemua: "",

        namaPTSolo: "",
        totalBukuSolo: "",
        totalUangSolo: "",
        listItemSolo: [],

        
        namaPTJakarta: "",
        totalBukuJakarta: "",
        totalUangJakarta: "",
        listItemJakarta: [],        
    },
    headerslistItem:[
      { text: "Nama Karyawan", value: "namaKaryawan" },
      { text: "Jumlah(Exp)", value: "totalBuku" },
      { text: "Harga satuan", value: "hargaSatuan" },
      { text: "Total Diskon", value: "totalDiskon" },
      { text: "Total Uang", value: "totalUang" },
    ],

    idSemester:"",
    idTahun:"",
    royalti: "",
    royaltiEdit:"",
    totalJualEdit:"",
    namaBukuEdit:"",
    idRoyaltiEdit:"",

    idRoyalti:"",

    snackbar: false,
    textsnackbar: "Test",
    timeoutsnackbar: 2000,
    colorsnackbar: null,

    itemsKlasifikasi: [],
  }),

  created() {
    this.authtoken = localStorage.getItem("token");
    this.BaseUrlGet = localStorage.getItem("BaseUrlGet");
    // this.getKlasifikasi();
    this.initialize();
  },

  methods: {
    async initialize() {
      try {
        const response = await axios.get(
          this.BaseUrlGet + "GetRoyalti?limit=all",
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        console.log(response.data.data.result.data);
        if (response.data.length != 0) {
          this.dataobject = response.data.data.result.data;
        } else {
          console.log("Kosong");
          this.dataobject = [];
        }
      } catch (error) {
        console.error(error);
        if (error.response.status == 401) {
          localStorage.removeItem("token");
          this.$router.push("/");
        }else {
          this.dataobject = [];
        }
      }
    },

    async getDetail(val) {
      this.idRoyalti = val.idRoyalti;
      try {
        const response = await axios.get(
          this.BaseUrlGet + "GetDetailRoyalti?idRoyalti="+this.idRoyalti,
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        console.log(response.data.data.result);
        if (response.data.length != 0) {
            this.detailItem.namaBuku = response.data.data.result.namaBuku;
            this.detailItem.royalti = response.data.data.result.royalti;
            this.detailItem.royaltiUang = response.data.data.result.royaltiUang;
            this.detailItem.totalBukuSemua = response.data.data.result.totalBukuSemua;
            this.detailItem.totalUangSemua = response.data.data.result.totalUangSemua;

            this.detailItem.namaPTSolo = response.data.data.result.listRoyaltiPerusahaan[0].namaPT;
            this.detailItem.totalBukuSolo = response.data.data.result.listRoyaltiPerusahaan[0].totalBuku;
            this.detailItem.totalUangSolo = response.data.data.result.listRoyaltiPerusahaan[0].totalUang;
            this.detailItem.listItemSolo = response.data.data.result.listRoyaltiPerusahaan[0].listItem;

            this.detailItem.namaPTJakarta = response.data.data.result.listRoyaltiPerusahaan[1].namaPT;
            this.detailItem.totalBukuJakarta = response.data.data.result.listRoyaltiPerusahaan[1].totalBuku;
            this.detailItem.totalUangJakarta = response.data.data.result.listRoyaltiPerusahaan[1].totalUang;
            this.detailItem.listItemJakarta = response.data.data.result.listRoyaltiPerusahaan[1].listItem;
        //   this.dataobject = response.data.data.result.data;

            this.dialogDetail = true;
        } else {
          console.log("Kosong");
        //   this.dataobject = [];
        }
      } catch (error) {
        console.error(error);
        if (error.response.status == 401) {
          localStorage.removeItem("token");
          this.$router.push("/");
        }else {
        //   this.dataobject = [];
        }
      }
    },

    async verifDelete() {
      const datapost = {
        id: this.idRoyalti,
      };
      console.log(datapost);
      // this.dialogDetail = false;
      try {
        const response = await axios.post(
          this.BaseUrlGet + "ResetRoyalti",
          datapost,
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        // console.log(response.data.data.result.data);
        if (response.data.data.result == "success") {
          this.dialogDelete = false;
          this.snackbar = true;
          this.colorsnackbar = "green";
          this.textsnackbar = "Sukses hapus data";
          this.initialize();
        } else {
          this.dialogDelete = false;
        }
      } catch (error) {
        console.error(error.response);
        if (error.response.status == 401) {
          //   this.dialogDelete = false;
          localStorage.removeItem("token");
          this.$router.push("/");
        }
      }
    },
    async save() {
      const datapost = {
        royalti: this.royalti,
        tahun: this.idTahun,
        semester: this.idSemester,
      };
      console.log(datapost);
      // this.dialogDetail = false;
      try {
        const response = await axios.post(
          this.BaseUrlGet + "AddAllBukuRoyalti",
          datapost,
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        console.log(response.data.data.result);
        if (response.data.data.result == "success") {
          this.dialog = false;
          this.snackbar = true;
          this.colorsnackbar = "green";
          this.textsnackbar = "Sukses tambah data";
          this.initialize();
        } else {
          this.dialog = false;
        }
      } catch (error) {
        console.error(error.response);
        if (error.response.status == 401) {
          //   this.dialog = false;
          localStorage.removeItem("token");
          this.$router.push("/");
        }
      }
    },
    async saveEdit() {
      const datapost = {
        idRoyalti: this.idRoyaltiEdit,
        royalti: this.royaltiEdit,
        totalJual: this.totalJualEdit,
      };
      console.log(datapost);
      // this.dialogDetail = false;
      try {
        const response = await axios.post(
          this.BaseUrlGet + "UpdateRoyalti",
          datapost,
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        console.log(response.data.data.result);
        if (response.data.data.result == "success") {
          this.dialog = false;
          this.snackbar = true;
          this.colorsnackbar = "green";
          this.textsnackbar = "Sukses tambah data";
          this.initialize();
          this.dialogEdit = false;
        } else {
          this.dialog = false;
          his.dialogEdit = false;
        }
      } catch (error) {
        console.error(error.response);
        if (error.response.status == 401) {
          //   this.dialog = false;
          localStorage.removeItem("token");
          this.$router.push("/");
        }
      }
    },
    

    showAddModal() {
      this.value = "add";
      this.dialog = true;
    },
    editItem(val){
        this.idRoyaltiEdit =val.idRoyalti;
        this.royaltiEdit =val.royalti;
        this.namaBukuEdit =val.judulBuku;
        this.totalJualEdit = val.totalJual;
        this.dialogEdit = true;
    },
    reset() {
      this.dialogDelete = true;
    },

    resetItemConfirm() {
      this.closeDelete();
      this.verifDelete();
    },
    close() {
      this.dialog = false;
      this.dialogEdit = false;
    },
    closeDelete() {
      this.dialogDelete = false;
    },

    getRupiah(val) {
        let str = val+"";
        const valsplit = str.split(".");
        var bilangan = valsplit[0];
	
        var	number_string = bilangan.toString(),
            sisa 	= number_string.length % 3,
            rupiah 	= number_string.substr(0, sisa),
            ribuan 	= number_string.substr(sisa).match(/\d{3}/g);
                
        if (ribuan) {
        var  separator = sisa ? '.' : '';
            rupiah += separator + ribuan.join('.');
        }
        
        return 'Rp. '+rupiah;
    },
  },
};
</script>