<template>
  <div>
    <v-breadcrumbs
      class="breadcrumbsmain"
      :items="itemsbr"
      divider=">"
      large
    ></v-breadcrumbs>

    <v-data-table
      :headers="headers"
      :items="dataobject"
      :search="search"
      class="rounded elevation-6 mx-3 pa-1"
    >

    <template v-slot:item.tanggal="{ item }">
          {{ gettanggal(item.tanggal) }}
      </template>

      <template v-slot:item.typeCust="{ item }">
          <v-chip :color="getColorStatus(item.customer)" dark> 
             {{ getTypeCust(item.customer) }} 
          </v-chip>          
      </template>   

      <template v-slot:item.harga="{ item }">
          {{ getRupiah(item.harga) }}
      </template>   

      <template v-slot:top>
        <v-toolbar flat>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          
        </v-toolbar>
      </template>
    </v-data-table>

    <v-snackbar
      v-model="snackbar"
      :color="colorsnackbar"
      :timeout="timeoutsnackbar"
    >
      {{ textsnackbar }}
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ReturnPenjualan",
  data: () => ({
    itemsbr: [
      {
        text: "Penjualan",
        disabled: true,
        href: "breadcrumbs_dashboard",
      },
      {
        text: "Return Penjualan",
        disabled: true,
        href: "breadcrumbs_link_1",
      },
    ],

    formTitle: "Add Item",
    value: "add",
    dialog: false,
    dialogVerifikasi: false,
    search: "",
    authtoken: "",
    BaseUrlGet: "",
    headers: [
      {
        text: "Nama Customer",
        value: "nama_customer",
      },
      { text: "Type Customer", value: "typeCust" },
      { text: "Type Trx", value: "type_trx" },
      { text: "Judul Buku", value: "judulBuku" },
      { text: "Jumlah Buku", value: "jumlahBuku" },
      { text: "Jumlah Uang", value: "harga" },
      { text: "Tanggal", value: "tanggal" },
      
    //   { text: "Actions", value: "actions", sortable: false },
    ],
    dataobject: [],


    snackbar: false,
    textsnackbar: "Test",
    timeoutsnackbar: 2000,
    colorsnackbar: null,

    // itemsCustomer: [],
  }),

  created() {
    this.authtoken = localStorage.getItem("token");
    this.BaseUrlGet = localStorage.getItem("BaseUrlGet");
    // this.getCustomer();
    this.initialize();
  },

  methods: {
    async initialize() {
      try {
        const response = await axios.get(
          this.BaseUrlGet + "GetReturnVerif?limit=all",
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        console.log(response.data.data.result.data);
        if (response.data.length != 0) {
          this.dataobject = response.data.data.result.data;
        } else {
          console.log("Kosong");
          this.dataobject = [];
        }
      } catch (error) {
        console.error(error);
        if (error.response.status == 401) {
          localStorage.removeItem("token");
          this.$router.push("/");

        }else {
          this.dataobject = [];
        }
      }
    },

    gettanggal(val) {
        var bulanIndo = ['', 'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September' , 'Oktober', 'November', 'Desember'];
 
        var date = val.split(" ")[0];
        var time = val.split(" ")[1];
      
        var  tanggal = date.split("-")[2];
        var  bulan = date.split("-")[1];
        var  tahun = date.split("-")[0];
      
          return tanggal + " " + bulanIndo[Math.abs(bulan)] + " " + tahun;
    },

    getTypeCust(val){
        var typeCust = '-'
        var str = val;
        var substr = str.substring(0, 2);        
        if(substr.toUpperCase()=='GD'){
            typeCust = 'Return Gudang'
        }else{
            typeCust = 'Return Customer'
        }
        return typeCust;
    },

    getColorStatus(val1){
        var str = val1;
        var substr = str.substring(0, 2);        
        if(substr.toUpperCase()=='GD'){
            return "green";
        }else{
            return "orange";
        }        
    },

    getRupiah(val) {
        var bilangan = val;
	
        var	number_string = bilangan.toString(),
            sisa 	= number_string.length % 3,
            rupiah 	= number_string.substr(0, sisa),
            ribuan 	= number_string.substr(sisa).match(/\d{3}/g);
                
        if (ribuan) {
        var  separator = sisa ? '.' : '';
            rupiah += separator + ribuan.join('.');
        }
        
        return 'Rp. '+rupiah;
    },

  },
};
</script>