<template>
  <div>
    <v-breadcrumbs
      class="breadcrumbsmain"
      :items="itemsbr"
      divider=">"
      large
    ></v-breadcrumbs>

    <v-data-table
      :headers="headers"
      :items="dataobject"
      :search="search"
      class="rounded elevation-6 mx-3 pa-1"
    >

    <template v-slot:item.created_at="{ item }">
          {{ gettanggal(item.created_at) }}
      </template>

      <template v-slot:item.typeCust="{ item }">
          <v-chip :color="getColorStatus(item.customer)" dark> 
             {{ getTypeCust(item.customer) }} 
          </v-chip>          
      </template>

      <template v-slot:item.typeVerif="{ item }">
          {{ gettypeVerif(item.typeVerif, item.customer) }}
      </template>      

      <template v-slot:top>
        <v-toolbar flat>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <!-- <v-btn dark class="mb-2" @click="showAddModal()" color="green">
            <v-icon small>mdi-plus</v-icon> Add Item
          </v-btn> -->
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline">Verifikasi Return</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                    <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <div>
                            <h5>Nama Customer</h5>
                            <h3 class="ml-2">{{ namaCustomer }}</h3>
                          </div>
                        </v-col>
                      </v-row>
                  <v-row>
                    <v-col cols="12" sm="8" md="8">
                      <v-select
                        v-model="typeTrx"
                        :items="itemstypeTrx"
                        item-value="value"
                        item-text="text"
                        label="Pilih Type Transaksi"
                        clearable
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="12" md="12">
                      <v-combobox
                        v-model="valCustomer"
                        :items="itemsCustomer"
                        item-value="codeCustomer"
                        item-text="namaCustomer"
                        label="Pilih Akun Customer"
                        clearable
                        :rules="[(v) => !!v || 'Field is required']"
                      ></v-combobox>
                    </v-col>
                     <v-col cols="12" sm="4" md="4">
                      <v-menu
                        v-model="menu2"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="date"
                            label="Pilih Tanggal"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="date"
                          @input="menu2 = false"
                        ></v-date-picker>
                      </v-menu>
                    </v-col>
                    <v-col cols="12" sm="8" md="8">
                      <v-text-field
                        v-model="totalUang"
                        label="Masukkan Jumlah Uang"
                        :rules="[(v) => !!v || 'Field is required']"
                        type="number"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="updateData"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogVerifikasi" max-width="500px">
            <v-card>
              <v-card-title class="headline"
                >Are you sure you want to verifikasi this?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="updateData"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon class="mr-2" @click="editItem(item)" color="warning">
          mdi-pencil
        </v-icon>
      </template>
    </v-data-table>

    <v-snackbar
      v-model="snackbar"
      :color="colorsnackbar"
      :timeout="timeoutsnackbar"
    >
      {{ textsnackbar }}
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ReturnPenjualan",
  data: () => ({
    itemsbr: [
      {
        text: "Penjualan",
        disabled: true,
        href: "breadcrumbs_dashboard",
      },
      {
        text: "Return Penjualan",
        disabled: true,
        href: "breadcrumbs_link_1",
      },
    ],

    formTitle: "Add Item",
    value: "add",
    dialog: false,
    dialogVerifikasi: false,
    search: "",
    authtoken: "",
    BaseUrlGet: "",
    headers: [
      {
        text: "Nama Customer",
        value: "namaCustomer",
      },
      { text: "Type Customer", value: "typeCust" },
      { text: "Verifikasi", value: "typeVerif" },
      { text: "Judul Buku", value: "judulBuku" },
      { text: "Jumlah Buku", value: "jumlahBuku" },
      { text: "Tanggal", value: "created_at" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    dataobject: [],

    // defaultItem: {
    //   id: "",
    //   idKlasifikasi: "",
    //   namaKlasifikasi: "",
    //   namaSubklasifikasi: "",
    // },

    date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    menu2: false,
    valCustomer:"",
    totalUang:"",
    idReturn:"",
    typeTrx:"",
    namaCustomer:"",

    snackbar: false,
    textsnackbar: "Test",
    timeoutsnackbar: 2000,
    colorsnackbar: null,

    itemsCustomer: [],
    itemstypeTrx : [
        { text: "Reguler", value: "Reguler" },
        { text: "Tempo", value: "Tempo" },
    ],
  }),

  created() {
    this.authtoken = localStorage.getItem("token");
    this.BaseUrlGet = localStorage.getItem("BaseUrlGet");
    this.getCustomer();
    this.initialize();
  },

  methods: {
    async initialize() {
      try {
        const response = await axios.get(
          this.BaseUrlGet + "GetReturnNonVerif?limit=all",
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        console.log(response.data.data.result.data);
        if (response.data.length != 0) {
          this.dataobject = response.data.data.result.data;
        } else {
          console.log("Kosong");
          this.dataobject = [];
        }
      } catch (error) {
        console.error(error);
        if (error.response.status == 401) {
          localStorage.removeItem("token");
          this.$router.push("/");

        }else {
          this.dataobject = [];
        }
      }
    },

    async getCustomer() {
      try {
        const response = await axios.get(
          this.BaseUrlGet + "DropdownAllCustomer",
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        console.log(response.data.data.result);
        if (response.data.length != 0) {
          this.itemsCustomer = response.data.data.result;
        } else {
          console.log("Kosong");
        }
      } catch (error) {
        console.error(error);
        if (error.response.status == 401) {
          //   this.alerttoken = true;
          localStorage.removeItem("token");
          this.$router.push("/");
        }
      }
    },

    async updateData() {
      const datapost = {
        idReturn: this.idReturn,
        code_customer: this.valCustomer.codeCustomer,
        nominal: this.totalUang,
        tanggal: this.date,
        typeTrx : this.typeTrx,
      };
      console.log(datapost);
      // this.dialogDetail = false;
      try {
        const response = await axios.post(
          this.BaseUrlGet + "UpdateVerifikasiReturnCustomer",
          datapost,
          {
            headers: {
              Authorization: `Bearer ` + this.authtoken,
            },
          }
        );
        // console.log(response.data.data.result);
        if (response.data.data.result == "success") {
          this.dialog = false;
          this.dialogVerifikasi = false;
          this.snackbar = true;
          this.colorsnackbar = "green";
          this.textsnackbar = "Sukses ubah data";
          this.initialize();
        } else {
          this.dialog = false;
        }
      } catch (error) {
        console.error(error.response);
        if (error.response.status == 401) {
          //   this.dialog = false;
          localStorage.removeItem("token");
          this.$router.push("/");
        }
      }
    },

    editItem(item) {
      console.log(item);
      this.formTitle = "Edit Item";
      this.idReturn = item.idReturn;
      this.namaCustomer = item.namaCustomer;
    //   this.defaultItem = Object.assign({}, item);
      this.value = "edit";
      
        var str = item.customer;
        var substr = str.substring(0, 2);        
        if(substr.toUpperCase()=='GD'){
          this.dialogVerifikasi = true;  
        }else{
           this.dialog = true; 
        }    
    },
    
    close() {
      this.dialog = false;
      this.dialogVerifikasi = false;
    },
    closeDelete() {
      this.dialogDelete = false;
    },
    // save() {
    //   if (this.defaultItem.namaSubklasifikasi.length != 0) {
    //     if (this.defaultItem.id) {
    //       console.log("edit");
    //       this.updateData();
    //     } else {
    //       console.log("add");
    //       this.addData();
    //     }
    //     this.close();
    //   } else {
    //     this.snackbar = true;
    //     this.colorsnackbar = "red";
    //     this.textsnackbar = "Gagal Menambah, Kolom tidak boleh ada yang kosong";
    //   }
    // },

    gettanggal(val) {
        var bulanIndo = ['', 'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September' , 'Oktober', 'November', 'Desember'];
 
        var date = val.split(" ")[0];
        var time = val.split(" ")[1];
      
        var  tanggal = date.split("-")[2];
        var  bulan = date.split("-")[1];
        var  tahun = date.split("-")[0];
      
          return tanggal + " " + bulanIndo[Math.abs(bulan)] + " " + tahun;
    },

    getTypeCust(val){
        var typeCust = '-'
        var str = val;
        var substr = str.substring(0, 2);        
        if(substr.toUpperCase()=='GD'){
            typeCust = 'Return Gudang'
        }else{
            typeCust = 'Return Customer'
        }
        return typeCust;
    },

    getColorStatus(val1){
        var str = val1;
        var substr = str.substring(0, 2);        
        if(substr.toUpperCase()=='GD'){
            return "green";
        }else{
            return "orange";
        }        
    },

    gettypeVerif(valA, valB){
        var str = valB;
        var substr = str.substring(0, 2);        
        if(substr.toUpperCase()=='GD'){
            return valA;
        }else{
            return valA;
        }
    }
  },
};
</script>